import { useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { useNavigate, useParams } from "react-router-dom"
import Button from "../../components/Button"
import TextField from "../../components/TextField"
import { editUser } from "./userSlice"

const EditUser = () => {
  const params = useParams();
  const dispatch = useDispatch();
  const users = useSelector(store => store.users);
  const navigate = useNavigate();
  const existingUser = users.filter(user => user.id === params.id);
  const { name, email, phone, address, company, designation } = existingUser[0];
  const [values, setValues] = useState({
    name,
    email,
 phone,
    address,
    company,
    designation
  });

  const handleEditUser = () => {
    setValues({ name: '', email: '', email:'', phone:'', address:'', company:'', designation:'' });
    dispatch(editUser({
      id: params.id,
      name: values.name,
      email: values.email,
      phone: values.phone,
      address: values.address,
      company: values.company,
      designation: values.designation
    }));
    navigate('/');
  }

  return (
    <div className="mt-10 max-w-xl mx-auto">
      <TextField
        label="Name"
        value={values.name}
        onChange={(e) => setValues({ ...values, name: e.target.value })}
        inputProps={{ type: 'text', placeholder: 'Jhon Doe' }}
      />
      <br />
      <TextField
        label="Email"
        value={values.email}
        onChange={(e) => setValues({ ...values, email: e.target.value })}
        inputProps={{ type: 'email', placeholder: 'jhondoe@mail.com' }}
      />
          <br />
      <TextField
        label="Phone number"
        value={values.phone}
        onChange={(e) => setValues({ ...values, Phone: e.target.value })}
        inputProps={{ type: 'Phone', placeholder: 'Phone' }}
      />
     <br />
      <TextField
        label="Address"
        value={values.address}
        onChange={(e) => setValues({ ...values, Address: e.target.value })}
        inputProps={{ type: 'Address', placeholder: 'Address' }}
      />
           <br />
      <TextField
        label="Company"
        value={values.company}
        onChange={(e) => setValues({ ...values, Company: e.target.value })}
        inputProps={{ type: 'Company', placeholder: 'Company' }}
      />
           <br/>
      <TextField
        label="Designation"
        value={values.designation}
        onChange={(e) => setValues({ ...values, Designation: e.target.value })}
        inputProps={{ type: 'Designation', placeholder: 'Designation' }}
      />
       <br/>
      <Button onClick={handleEditUser}>Edit</Button>
    </div>
  )
}

export default EditUser




