import { useState } from "react"
import { useDispatch } from "react-redux"
import { useNavigate } from "react-router-dom"
import { v4 as uuidv4 } from 'uuid';
import Button from "../../components/Button"
import TextField from "../../components/TextField"
import { addUser } from "./userSlice"

const AddUser = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [values, setValues] = useState({
    name: '',
    email: '',
    phone: '',
    address: '',
    company: '',
    designation: ''
  });

  const handleAddUser = () => {
    setValues({ name: '', email: '' });
    dispatch(addUser({
      id: uuidv4(),
      name: values.name,
      email: values.email,
      phone: values.phone,
      address: values.address,
      company: values.company,
      designation: values.designation
    }));
    navigate('/');
  }

  return (
    <div className="mt-10 max-w-xl mx-auto">
      <TextField
        label="Name"
        value={values.name}
        onChange={(e) => setValues({ ...values, name: e.target.value })}
        inputProps={{ type: 'text', placeholder: 'Jhon Doe' }}
      />
      <br />
      <TextField
        label="Email"
        value={values.email}
        onChange={(e) => setValues({ ...values, email: e.target.value })}
        inputProps={{ type: 'email', placeholder: 'jhondoe@mail.com' }}
      />
         <br />
      <TextField
        label="Phone"
        value={values.phone}
        onChange={(e) => setValues({ ...values, phone: e.target.value })}
        inputProps={{ type: 'phone', placeholder: 'Enter phone number' }}
      />
         <br />
      <TextField
        label="Address"
        value={values.address}
        onChange={(e) => setValues({ ...values, address: e.target.value })}
        inputProps={{ type: 'email', placeholder: 'Enter Address' }}
      />
         <br />
      <TextField
        label="Company"
        value={values.company}
        onChange={(e) => setValues({ ...values, company: e.target.value })}
        inputProps={{ type: 'email', placeholder: 'Enter company name' }}
      />
           <br />
      <TextField
        label="Designation"
        value={values.designation}
        onChange={(e) => setValues({ ...values, designation: e.target.value })}
        inputProps={{ type: 'email', placeholder: 'Enter your designation' }}
      />
      <Button onClick={handleAddUser}>Submit</Button>
    </div>
  )
}

export default AddUser

